import GettingStartedSchema from '../images/GettingStarted/GettingStartedSchema.webp';
import {
  GettingStartedBottomDescription,
  GettingStartedDivider,
  GettingStartedFootNote,
  GettingStartedLink,
  GettingStartedLinkArrow,
  GettingStartedSchemaDesktop,
  GettingStartedSteps,
  GettingStartedTitleBlock,
} from '../components/getting-started/styled';
import RightLongArrow from '../components/common/Icons/Arrows/RightLongArrow';
import TwilioLogo from '../images/Logo/Others/Twilio.png';
