import React, { FC } from "react";
import TimerIcon from "../common/Icons/TimerIcon/TimerIcon";
import {
  StepBody,
  StepDescription,
  StepDuration,
  StepNumber,
  StepTitle,
  StyledGettingStartedLabel,
  StyledGettingStartedStep,
} from "./styled";

interface GettingStartedStepProps {
  stepNumber: number;
  stepTitle: string;
  stepDescription: string;
  stepDuration: string;
}

interface GettingStartedLabelProps {
  labelText: string;
}

export const GettingStartedStep: FC<GettingStartedStepProps> = (props) => {
  const { stepDescription, stepDuration, stepNumber, stepTitle } = props;

  return (
    <StyledGettingStartedStep>
      <StepNumber>
        <span>{stepNumber}</span>
      </StepNumber>
      <StepBody>
        <StepTitle>{stepTitle}</StepTitle>
        <StepDescription>{stepDescription}</StepDescription>
        <StepDuration>
          <TimerIcon />
          {stepDuration}
        </StepDuration>
      </StepBody>
    </StyledGettingStartedStep>
  );
};

export const GettingStartedLabel: FC<GettingStartedLabelProps> = ({
  labelText,
}) => {
  return <StyledGettingStartedLabel>{labelText}</StyledGettingStartedLabel>;
};
